import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
const routes = [
  { 
    path: '/',
    component: resolve => require(['@/views/Menu'], resolve),
    children: [
      {
        path: '/', // 默认子路由
        component: resolve => require(['@/views/mining/mining.vue'], resolve)
      },
      {
        path: '/Home',
        component: resolve => require(['@/views/Home'], resolve)
      },
      {
        path: '/Mix',
        component: resolve => require(['@/views/Mix'], resolve)
      },
      {
        path: '/Flowmining',
        component: resolve => require(['@/views/mining/mining.vue'], resolve)
      },
      {
        path: '/Market',
        component: resolve => require(['@/views/Market'], resolve)
      },
    ]
  }

]

const router = new VueRouter({
  routes
})

export default router
