import {
  Checkbox,
  CheckboxButton,
  CheckboxGroup,
  Message,
  MessageBox,
  Notification,
  Radio,
  RadioGroup,
  RadioButton,
  Switch,
  Table,
  TableColumn,
  Input,
  InputNumber,
  Popover,
  Button,
  ButtonGroup,
  Link,
  Dialog,
  Select,
  Option,
  OptionGroup,
} from 'element-ui'
const element = {
  install: function(Vue) {
    Vue.use(Radio)
    Vue.use(RadioGroup)
    Vue.use(RadioButton)
    Vue.use(Checkbox)
    Vue.use(CheckboxButton)
    Vue.use(CheckboxGroup)

    Vue.use(Switch)
    Vue.use(Table)
    Vue.use(TableColumn)
    Vue.use(Input)
    Vue.use(InputNumber)
    Vue.use(Popover)

    Vue.use(Button)
    Vue.use(ButtonGroup)
    Vue.use(Link)
    Vue.use(Dialog)
    Vue.use(Select)
    Vue.use(Option)
    Vue.use(OptionGroup)



    
    Vue.prototype.$message = Message
    Vue.prototype.$confirm = MessageBox.confirm
    Vue.prototype.$alert = MessageBox.alert
    Vue.prototype.$notify = Notification
  }
}

export default element

