import Axios from 'axios'
import md5 from 'js-md5';
Axios.defaults.timeout = 10000;   // 超时时间
// bsc测试网


Axios.defaults.baseURL = 'https://api.pokemongo.farm/api';

let app_secret =  '00001PWDSECRETFLOW';
let appid = '00001';


function getTimestamp10(){
  const NOW =  Math.round(new Date().getTime()/1000).toString();
  return NOW;
}

// create an axios instance
Axios.interceptors.request.use(
  config => {
  let time = getTimestamp10();
  let sign = appid+app_secret+time;
  sign = md5(sign);
  config.headers['appid'] = appid;
  config.headers['time'] = time;
  config.headers['sign'] = sign;
 
 
 
    return config;
  },
  error => {
    // 对请求错误做些什么，自己定义
    console.log(error);
    return Promise.reject(error);
  })
  
Axios.interceptors.response.use(
  response => {
    
    return response;
  },
  error => {
    // 对请求错误做些什么，自己定义
    console.log(error);
    return Promise.reject(error);
  })
  
export default Axios;