import DApp from '../config'

const API = {
  async pending(_pid,user) {
    try {
      const { pending } = this.meta.methods;
      const res = await pending(_pid,user).call();
      return res
    } catch (e) {
      console.log(e);
    }
    
  },
  async userInfo(pid,account) {
    try {
      const { userInfo } = this.meta.methods;
      const res = await userInfo(pid,account).call();
      return res;
    } catch (e) {
      console.log(e);
    }
    
  },
  async deposit(_pid,_amount) {
    try {
      // console.log(_pid,_amount);
      const { deposit } = this.meta.methods
      const { account } = this
      const gasAmount = await deposit(_pid,_amount).estimateGas({ from: account })
      const res = await deposit(_pid,_amount).send({ from: account, gas: gasAmount })
      return res
    } catch (e) {
      console.log(e);
    }
    
  },
  async withdraw(_pid,_amount) {
    try {
      const { withdraw } = this.meta.methods
      const { account } = this
      const gasAmount = await withdraw(_pid,_amount).estimateGas({ from: account })
      const res = await withdraw(_pid,_amount).send({ from: account, gas: gasAmount })
      return res
    } catch (e) {
      console.log(e);
    }
    
  },
  
}

export default {
  meta: null,
  account: null,
  web3: null,
  async init() {
    const obj = await DApp.flowInit()
    const { meta, account, web3 } = obj
    this.meta = meta
    this.account = account
    this.web3 = web3
    return this
  },
  ...API

}
