
import Web3Init from './init.js'

Web3Init.url = 'https://www.oklink.com/okexchain/';
import { configabi } from "../../abi/configabi";

const {
    OecPoolAddress,OecPool,
    Erc20,
    flowAddress,flow,
} = configabi;

export default{
    async OecPoolInit() {
        const instance = new Web3Init({
          // 参数引入合约
          address: OecPoolAddress,
          abi: OecPool
        })
        const obj = await instance.init()
    
        return obj
      },
      async LPInit(address) {
        const instance = new Web3Init({
          // 参数引入合约
          address,
          abi: Erc20
        })
        const obj = await instance.init()
    
        return obj
      },
      async flowInit() {
        const instance = new Web3Init({
          // 参数引入合约
          address:flowAddress,
          abi: flow
        })
        const obj = await instance.init()
    
        return obj
      },
}